<template>
  <div class="roles">
    <list @delid="roleder"></list>
    <addbuttom @addnutton="add"></addbuttom>

    <add :isShow="isShow" ref="one"></add>
  </div>
  
</template>

<script>
import add from "./components/add";
import list from "./components/list";

export default {
  data() {
    return {
      isShow: {
        flag: false,
        edit: true,
      },
    };
  },
  components: {
    add,
    list,
  },
  methods: {
     add() {
      // 在添加的时候 修改控制 add 显示隐藏 点击 添加的时候 让 flag变成true
      // 并让 edit 为true 也就是 添加菜单
      this.isShow.flag = true;
      this.isShow.edit = true;
    },

    roleder(id) {
      this.isShow.flag = true;
      this.isShow.edit = false;
      this.$refs.one.getOne(id);
    },
  },
};
</script>

<style scoped>
.roles {
  padding: 0px 150px;
}
</style>


