<template>
  <div>
    <el-dialog
      :title="isShow.edit ? '添加商品' : '修改商品'"
      :visible.sync="isShow.flag"
    >
      <el-form class="select1">
        <el-form-item label="上级菜单">
          <el-select v-model="form.pid" placeholder="请选择">
            <el-option label="顶级菜单" :value="0"></el-option>
            <el-option
              v-for="item in CateGetter"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <el-form ref="form" label-width="100px">
        <el-form-item label="分类名称">
          <el-input v-model="form.catename"></el-input>
        </el-form-item>

        <el-form-item>
          <el-upload
            class="avatar-uploader"
            action="#"
            :show-file-list="false"
            :on-change="changimg"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>

        <el-form-item label="状态" prop="delivery">
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="2"
          ></el-switch>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" v-if="isShow.edit" @click="add()"
          >添 加</el-button
        >
        <!-- 修改按钮 通过 v-if控制  -->
        <el-button type="primary" @click="edit()" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { reqCateAdd, reqCateOne, reqCateEdit } from "../../../util/request";
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";

export default {
  props: ["isShow"],
  data() {
    return {
      form: {
        catename: "",
        img: "",
        pid: 0,
        status: 1,
      },
      defaultProps: {
        children: "children",
        label: "title",
      },
      imageUrl: "",
    };
  },

  computed: {
    ...mapGetters({
      CateGetter: "cate/CateGetter",
    }),
  },

  mounted() {
    this.actionCateList();
  },

  methods: {
    ...mapActions({
      actionCateList: "cate/actionCateList",
    }),

    changimg(file) {
      // 通过 url.createObje 渲染
      const isLt2M = file.size / 1024 / 1024 < 1;

      if (!isLt2M) {
        warningAlert("请上传2M内的文件");
        return;
      }

      this.imageUrl = URL.createObjectURL(file.raw);
      this.form.img = file.raw;
    },

    clear() {
      this.form = {
        catename: "",
        img: "",
        pid: 0,
        status: 1,
      };
      this.imageUrl = "";
    },

    // 点击取消之后进行清空
    cancel() {
      this.isShow.flag = false;
      this.clear();
    },

    // 点击添加执行结果
    add() {
      reqCateAdd(this.form).then((res) => {
        if (res.data.code == 200) {
          this.actionCateList();
          successAlert(res.data.msg);
          this.isShow.flag = false;
          this.clear();
          // 重新调用刷新
        } else {
          warningAlert(res.data.msg);
          this.isShow.flag = false;
          this.clear();
        }
      });
    },
    // 获取一个
    getOne(id) {
      // // 首先应该先获取其中一条 然后进行 先渲染 然后在进行提交修改
      // // 1.获取其中 一条
      reqCateOne(id).then((res) => {
        this.form = res.data.list;

        // 进行加载图片
        this.imageUrl = this.$baseimg + res.data.list.img;
        this.form.id = id;
      });
    },

    // 修改点击提交
    edit() {
      console.log(this.form);
      reqCateEdit(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.isShow.flag = false;
          this.clear();

          // 重新调用刷新
          this.actionCateList();
        } else {
          warningAlert(res.data.msg);
          this.isShow.flag = false;
          this.clear();
        }
      });
    },
  },
};
</script>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.select1 {
  margin-left: 30px;
}
</style>



